<template>
  <v-card>
    <validation-observer
      ref="observer"
      v-slot="{ invalid }"
    >
      <v-card-title>
        Outgoing Vaccines Form
      </v-card-title>

      <v-card-text>
        <v-alert
          v-model="alert.isOpen"
          border="left"
          :type="alert.type"
          :color="alert.color"
          dismissible
        >
          {{alert.message}}
        </v-alert>

        <v-row>
          <v-col cols="6">
            <validation-provider
              name="Receiving team"
              v-slot="{ errors }"
              rules="required"
            >
              <v-select
                label="Receiving team*"
                v-model="form.team_id"
                item-text="name"
                item-value="id"
                :items="teams"
                :disabled="isSaving"
                :error-messages="errors"
              />
            </validation-provider>
          </v-col>

          <v-col cols="6">
            <validation-provider
              name="Storage Unit"
              v-slot="{ errors }"
              rules="required"
            >
              <v-select
                label="Storage Unit*"
                v-model="form.storage_unit_id"
                item-text="name"
                item-value="id"
                :items="storageUnits"
                :disabled="isSaving"
                :error-messages="errors"
                @change="storageUpdate"
              />
            </validation-provider>
          </v-col>

          <v-col cols="6">
            <validation-provider
              name="Manufacturer"
              v-slot="{ errors }"
              rules="required"
            >
              <v-select
                label="Manufacturer*"
                v-model="form.manufacturer"
                :items="manufacturers"
                :disabled="isSaving || !form.storage_unit_id"
                :error-messages="errors"
                @change="mfrUpdate"
              />
            </validation-provider>
          </v-col>

          <v-col cols="6">
            <validation-provider
              name="Serial"
              v-slot="{ errors }"
              rules="required"
            >
              <v-select
                label="Serial*"
                v-model="form.serial"
                :items="serials"
                :disabled="isSaving || !form.manufacturer"
                :error-messages="errors"
                @change="serialUpdate"
              />
            </validation-provider>
          </v-col>

          <v-col cols="6">
            <validation-provider
              name="Lot Number"
              v-slot="{ errors }"
              rules="required"
            >
              <v-select
                label="Lot Number*"
                v-model="form.lot_no"
                :items="lots"
                :disabled="isSaving || !form.serial"
                :error-messages="errors"
                @change="lotUpdate"
              />
            </validation-provider>
          </v-col>

          <v-col cols="6">
            <validation-provider
              name="Amount"
              v-slot="{ errors }"
              :rules="`required|min_value:1|max_value:${vaccineAmount}`"
            >
              <v-text-field
                label="Amount*"
                v-model="form.amount"
                :disabled="isSaving || !vaccineAmount"
                :error-messages="errors"
                :hint="!!vaccineAmount ? `Available ${vaccineAmount}`: ''"
              />
            </validation-provider>
          </v-col>

          <v-col cols="12">
            <v-text-field
              label="Notes"
              v-model="form.comments"
              :disabled="isSaving"
            />
          </v-col>
        </v-row>

        <span class="text-caption">
          * Field is required
        </span>
      </v-card-text>

      <v-card-actions>
        <v-spacer />

        <v-btn
          color="primary"
          :disabled="isSaving"
          @click="clear"
        >
          Clear
        </v-btn>

        <v-btn
          color="success"
          :disabled="invalid || isSaving"
          :loading="isSaving"
          @click="save"
        >
          Save
        </v-btn>
      </v-card-actions>
    </validation-observer>
  </v-card>
</template>

<script>
import {ValidationObserver, ValidationProvider} from 'vee-validate'
import axios from 'axios'

export default {
  name: 'CsmOutgoingVaccinesForm',

  components: {
    ValidationProvider,
    ValidationObserver,
  },

  data: () => ({
    alert: {
      isOpen: false,
      type: 'success',
      message: 'Outgoing vaccine log saved',
      color: 'success',
    },
    form: {
      team_id: undefined,
      storage_unit_id: undefined,
      manufacturer: '',
      serial: '',
      lot_no: '',
      amount: undefined,
      comments: '',
    },
    isSaving: false,
    teams: [],
    storageUnits: [],
    manufacturers: [],
    serials: [],
    lots: [],
    vaccines: [],
    vaccineAmount: 0,
  }),

  mounted () {
    this.fetchStorageUnits()
    this.fetchTeams()
  },

  methods: {
    async save () {
      this.isSaving = true
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const data = {
          ...this.form,
          transaction_type: 'outgoing',
        }

        await axios.post(
          `${apiHost}/vaccine-logs`,
          data,
          config
        )

        this.successAlert()
        this.clear()
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login').catch()
        } else if (error.response.status === 400) {
          this.alert.type = 'error'
          this.alert.message = error.response.data.message
          this.alert.isOpen = true
        } else {
          this.somethingWentWrongAlert()
        }
      }

      this.isSaving = false
    },

     async fetchStorageUnits () {
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/storage-units`, config)
        this.storageUnits = data
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login').catch()
          return
        }
        this.somethingWentWrongAlert()
      }
    },

    async fetchTeams () {
      const apiHost = process.env.VUE_APP_API_HOST
      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const result = await axios.get(
          `${apiHost}/teams`,
          config
        )

        this.teams = result.data
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/admin/login')

          return
        }

        this.somethingWentWrongAlert()
      }
    },

    somethingWentWrongAlert() {
      this.alert.type = 'warning'
      this.alert.message = 'Something went wrong. Please contact your IT admin.'
      this.alert.isOpen = true
    },

    successAlert() {
      this.alert.type = 'success'
      this.alert.message = 'Form successfully submitted'
      this.alert.isOpen = true
    },

    clear() {
      this.form.team_id = undefined
      this.form.storage_unit_id = undefined
      this.form.amount = undefined
      this.form.manufacturer = ''
      this.form.serial = ''
      this.form.lot_no = ''
      this.form.comments = ''
      this.$refs.observer.reset()
    },

    async storageUpdate() {
      if (!this.form.storage_unit_id) return
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(
          `${apiHost}/vaccine-logs/storage-units/${this.form.storage_unit_id}`,
          config
        )

        this.form.serial = ''
        this.form.lot_no = ''
        this.form.manufacturer = ''
        this.manufacturers = data.map(v => v.manufacturer)
        this.vaccines = data
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login').catch()
        }
        this.somethingWentWrongAlert()
      }
    },

    async mfrUpdate () {
      const {manufacturer} = this.form
      if (!this.form.manufacturer) return

      this.serials = this.vaccines
        .filter(v => v.manufacturer === manufacturer)
        .map(v => v.serial)

      this.form.serial = ''
      this.form.lot_no = ''
    },

    async serialUpdate() {
      const {manufacturer, serial} = this.form
      if (!this.form.serial) return

      this.lots = this.vaccines
        .filter(v => v.manufacturer === manufacturer && v.serial === serial)
        .map(v => v.lot_no)
      

    },

    async lotUpdate() {
      const {storage_unit_id, manufacturer, serial, lot_no} = this.form
      if (!this.form.lot_no) return

      this.lots = this.vaccines
        .filter(v => v.manufacturer === manufacturer)
        .filter(v => v.serial === serial)
        .filter(v => v.lot_no === lot_no)
        .map(v => v.lot_no)

      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(
          `${apiHost}/vaccine-logs/storage-units/${storage_unit_id}/manufacturers/${manufacturer}/serials/${serial}/lots/${lot_no}`,
          config
        )

        this.vaccineAmount = data.vaccine.amount
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login').catch()
        }
        this.somethingWentWrongAlert()
      }
    }
  }

}
</script>
