<template>
  <v-card>
    <validation-observer
      ref="observer"
      v-slot="{ invalid }"
    >
      <v-card-title>
        Incoming Vaccines Form
      </v-card-title>

      <v-card-text>
        <v-alert
          v-model="alert.isOpen"
          border="left"
          :type="alert.type"
          :color="alert.color"
          dismissible
        >
          {{alert.message}}
        </v-alert>

        <v-row>
          <v-col cols="6">
            <validation-provider
              name="Storage Unit"
              v-slot="{ errors }"
              rules="required"
            >
              <v-select
                label="Storage Unit*"
                v-model="form.storage_unit_id"
                item-text="name"
                item-value="id"
                :items="storageUnits"
                :disabled="isSaving"
                :error-messages="errors"
              />
            </validation-provider>
          </v-col>

          <v-col cols="6">
            <validation-provider
              name="Amount"
              v-slot="{ errors }"
              rules="required|numeric"
            >
              <v-text-field
                label="Amount*"
                v-model="form.amount"
                :disabled="isSaving"
                :error-messages="errors"
              />
            </validation-provider>
          </v-col>

          <v-col cols="6">
            <validation-provider
              name="Manufacturer"
              v-slot="{ errors }"
              rules="required"
            >
              <v-select
                label="Manufacturer*"
                v-model="form.manufacturer"
                :items="manufacturers"
                :disabled="isSaving"
                :error-messages="errors"
              />
            </validation-provider>
          </v-col>

          <v-col cols="6">
            <validation-provider
              name="Expiration"
              v-slot="{ errors }"
              rules="required|logs_expiration_date"
            >
              <v-text-field
                label="Expiration*"
                v-model="form.expiration"
                :disabled="isSaving"
                :error-messages="errors"
              />
            </validation-provider>
          </v-col>

          <v-col cols="6">
            <validation-provider
              name="Serial"
              v-slot="{ errors }"
              rules="required"
            >
              <v-text-field
                label="Serial*"
                v-model="form.serial"
                :disabled="isSaving"
                :error-messages="errors"
              />
            </validation-provider>
          </v-col>

          <v-col cols="6">
            <validation-provider
              name="Lot Number"
              v-slot="{ errors }"
              rules="required"
            >
              <v-text-field
                label="Lot Number*"
                v-model="form.lot_no"
                :disabled="isSaving"
                :error-messages="errors"
              />
            </validation-provider>
          </v-col>

          <v-col cols="12">
            <v-text-field
              label="Notes"
              v-model="form.comments"
              :disabled="isSaving"
            />
          </v-col>
        </v-row>

        <span class="text-caption">
          * Field is required
        </span>
      </v-card-text>

      <v-card-actions>
        <v-spacer />

        <v-btn
          color="primary"
          :disabled="isSaving"
          @click="clear"
        >
          Clear
        </v-btn>

        <v-btn
          color="success"
          :disabled="invalid || isSaving"
          :loading="isSaving"
          @click="save"
        >
          Save
        </v-btn>
      </v-card-actions>
    </validation-observer>
  </v-card>
</template>

<script>
import {ValidationObserver, ValidationProvider} from 'vee-validate'
import axios from 'axios'

import VaccineMfrs from '../../constants/VaccineMfr'


export default {
  name: 'CsmIncomingVaccinesForm',

  components: {
    ValidationProvider,
    ValidationObserver,
  },

  data: () => ({
    alert: {
      isOpen: false,
      type: 'success',
      message: 'Incoming vaccine log saved',
      color: 'success',
    },
    form: {
      storage_unit_id: undefined,
      amount: undefined,
      comments: '',
      expiration: '',
      manufacturer: '',
      serial: '',
      lot_no: '',
    },
    isSaving: false,
    storageUnits: [],
    manufacturers: VaccineMfrs
  }),

  mounted () {
    this.fetchStorageUnits()
  },

  methods: {
    async save () {
      this.isSaving = true
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const data = {
          ...this.form,
          transaction_type: 'incoming',
        }

        await axios.post(
          `${apiHost}/vaccine-logs`,
          data,
          config
        )

        this.form.serial = ''
        this.form.lot_no = ''
        this.$refs.observer.reset()
        this.successAlert()
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login').catch()
        } else if (error.response.status === 400) {
          this.alert.type = 'error'
          this.alert.message = error.response.data.message
          this.alert.isOpen = true
        } else {
          this.somethingWentWrongAlert()
        }
      }

      this.isSaving = false
    },

     async fetchStorageUnits () {
      const apiHost = process.env.VUE_APP_API_HOST

      const token = localStorage.getItem('dms-token')
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      }

      try {
        const {data} = await axios.get(`${apiHost}/storage-units`, config)
        this.storageUnits = data
      } catch (error) {
        if (error?.response?.status === 401) {
          localStorage.removeItem('dms-token')
          this.$router.push('/csm/login').catch()
        }
        this.somethingWentWrongAlert()
      }
    },

    somethingWentWrongAlert() {
      this.alert.type = 'warning'
      this.alert.color = 'warning'
      this.alert.message = 'Something went wrong. Please contact your IT admin.'
      this.alert.isOpen = true
    },

    successAlert() {
      this.alert.type = 'success'
      this.alert.color = 'success'
      this.alert.message = 'Form successfully submitted'
      this.alert.isOpen = true
    },

    clear() {
      this.form.storage_unit_id = undefined
      this.form.amount = undefined
      this.form.manufacturer = ''
      this.form.expiration = ''
      this.form.serial = ''
      this.form.lot_no = ''
      this.form.comments = ''
    }
  }

}
</script>
